//background the section 1


.green{
  color: green;
}
.section-1-home{
  background-image: url(../../img/dist/source/buffy_cover2.jpg);
  background-size: 100%;
  min-height: calc(100vh - 52px);
  width: 100%;
  background-repeat: no-repeat;
  margin-top: 5%;
}
.msg-connexion{
  padding-top: 2%;
  padding-left: 15px;
  font-size: 17px;

}

//button section1

.content-btn-nav-sect-1{
  display: flex;
  justify-content: space-around;
  padding-top: 17%;

}
.btn-nav-sec-1{
  padding: 12px 25px;

  border-radius: 30px;
  color: $white;
  transition-duration: .4s;
  text-transform: uppercase;
  font-family: rbt-r;
  font-size: 20px;
}
.bgc-btn-sec1{
  background-color: $red;
}
.bgc-btn-sec2{
  background-color: $green;
  padding: 12px 40px;
  color: $black;
}
.bgc-btn-sec1:hover{
  background-color: $green;
  color: $black;
  text-decoration: none;
}
.bgc-btn-sec2:hover{
  background-color: $red;
  color: $white;
  text-decoration: none;

}

// section 2 title and caroussel
.section-2-home{
  padding-top: 4%;
}
.content-title-home{
  background-color: $green-o;
}

.title-home{
  color: $black;
  text-align: center;
  text-transform: uppercase;
  padding: 40px 0px;
  font-size: 65px;
  letter-spacing: 7px;
  font-family: lato-bold;
  color: rgba(0, 0, 0, 0.67);
}
.spacing-home{
  padding-top: 100px;
  display: flex;
  justify-content: center;

}
.content-text-personage{
  border-top: 3px solid $black;
  border-bottom: 3px solid $black;
  width: 80%;
  background-image: url(../../img/dist/source/25yr.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-color: rgba(25,38,50,.7);
  filter: blur(5px);
}
.title-personage{
  text-transform: uppercase;
  font-size: 45px;
  text-align: center;
  padding: 30px 0px;
  font-family: lato-b;
  color: $white;

}


//carousel


.cards-wrapper {
  display: flex;
  justify-content: center;
}
.card img {
  max-width: 100%;
  max-height: 100%;
}
.card {
  margin: 0 1.5em;
  box-shadow: 2px 6px 8px 0 rgba(22, 22, 26, 0.18);
  border: none;
  border-radius: 5px;
}
.carousel-inner {
  padding: 1em;
}
.carousel-control-prev,
.carousel-control-next {
  background-color: #e1e1e1;
  width: 5vh;
  height: 5vh;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}
.bg-carousel{
  background: linear-gradient($white, $brown-o , $white);
  padding: 150px 0;
  margin-top: 100px;
}

.card-title{
  font-size: 20px;
  text-align: center;
  margin-bottom: 25px;
}

.content-link-carousel{
  display: flex;
  justify-content: center;

}

.btn-carousel{
  background-color: rgba(59, 7, 7, 0.74);
  color: $white;
  padding: 10px 15px;
  border-radius: 25px;
  transition-duration: .5s;
  font-size: 13px;
  font-family: "Roboto Thin";
  font-weight: bold;
}
.btn-carousel:hover{
  background-color: $green;
  color: $black ;

}.btn-carousel2{
   background-color: $green;
  color: $white;
  padding: 10px 15px;
  border-radius: 25px;
  transition-duration: .5s;
  font-size: 13px;
  font-family: "Roboto Thin";
  font-weight: bold;
}
.btn-carousel2:hover{

  background-color: rgba(59, 7, 7, 0.74);
  color: $white ;

}



.section-3-h{
  width: 80%;
  margin: auto;
}


.content-title-acces-blog{
  border-top: 3px solid $black;
  border-bottom: 3px solid $black;

  margin: 100px auto;
}
.title-acces-blog{
  text-transform: uppercase;
  font-size: 60px;
  text-align: center;
  padding: 30px 0px;

}
.content-img-acces-blog{
  background-color: $brown;
  width: 90%;
  margin: auto;
  border-radius: 10px;
}
.content-img-flex-blog{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.img-acces-blog{
  width: 50%;
  margin: 75px 0px;
  padding-left: 90px;


}
.content-button-acces-blog{
  margin: auto;
}
.button-acces-blog{
  padding: 15px 50px;
  border-radius: 30px;
  color: $black;
  transition-duration: .4s;
  background-color: $green;
  display: inline-block;
  font-size: 20px;
  font-family: rbt-r;

}
.button-acces-blog:hover{
  background-color: $red;
  text-decoration: none;
  color: $white;
}

//section 4

.section-4-home{
  @extend .section-3-h;
}


.content-title-acces-contact{
  @extend .content-title-acces-blog;
}
.title-acces-contact{
  font-size: 60px;
  text-align: center;
  padding: 30px 0px;
}
.content-contact{
  display: flex;
  justify-content: center;
}
.content-img-flex-contact{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $green-o;
  border-radius: 10px;
  width: 90%;

}

.img-acces-contact{
  margin: auto;
  padding: 75px 0px;

  width: 50%;
  border-radius: 7px;

}
.content-button-acces-contact{
  margin: auto;
}
.button-acces-contact{
  padding: 15px 40px;
  font-size: 20px;
  font-family: rbt-r;
  border-radius: 30px;
  color: $white;
  transition-duration: .4s;
  background-color: $red;
  display: inline-block;
  text-transform: uppercase;

}
.button-acces-contact:hover{
  background-color: $green;
  text-decoration: none;
  color: $black;


}

//media query page home

@media screen and (max-width: 1399px) {
}

@media screen and (max-width: 1299px) {
  .section-1-home{
    margin-bottom: -15%;
  }
}
@media screen and (max-width: 1199px) {
  .section-1-home{
    margin-bottom: -19%;
  }
  .btn-nav-sec-1{
    font-size: 10px;
  }
}
@media screen and (max-width: 1023px) {
  .section-1-home{
    margin-bottom: -33%;
  }
  .content-img-flex-blog{
    flex-direction: column;
  }
  .img-acces-blog{
    padding-left: 0px;
    margin: 75px 0 50px 0;

  }
  .content-button-acces-blog{
    margin-bottom: 50px;
  }
  .content-img-flex-contact{
    flex-direction: column-reverse;

    .content-button-acces-contact{
      margin-bottom: 50px;
    }
  }
  .img-acces-contact{
    margin: 75px 0 50px 0;
    padding: 0;
  }
}
@media screen and (max-width: 991px) {
  .section-1-home{
    margin-bottom: -36%;
  }
}
@media screen and (max-width: 767px) {
  .section-1-home{
    margin-bottom: -60%;
  }

}@media screen and (max-width: 657px) {
  .section-1-home{
    margin-bottom: -79%;
  }
  .img-acces-blog{
    width: 70%;
  }
  .img-acces-contact{
    width: 70%;
  }
}
@media screen and (max-width: 576px) {
  .section-1-home{
    margin-bottom: -95%;
  }
}